body, h1, h2, h3, h4, h5, h6, p, ul, ol, table, dl, dd {
	margin: 0;
	padding: 0;
}
h1 {
	font-size: 16px;
}
a {
	text-decoration: none;
	color: #000;
}
ul, ol, li {
	list-style: none;
}
img {
	max-width: 100%;
	vertical-align: top;
	border: none;
}
button{
	border: none;
	background-color: #333;
	transition: background-color 0.8s ease-in-out;
}
button.bt{
	border: none;
	background-color: #eaeaea;
}


body {
	font-family:'Noto Sans KR', sans-serif;
	background-color: #eaeaea;
	transition: background-color 0.8s ease-in-out;
}

#dim {
	display: none;
	position: fixed;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
	background-color: rgba(0,0,0,.6);
	z-index: 9999;
}
#dim.dt {
	display: block;
}

/* #191970 */
body.white {
	background-color: #333;
}

body.fixed {
	overflow-y: hidden;
	overflow-x: hidden;
}

#main{
	display: flex;
	justify-content: center;
	align-items: center;
	height: 100vh;
}

#main .main_inner{
	max-width: 1200px;
	margin: 0 auto;
	text-align: center;
}

#main header{
	position: fixed;
	width: 100%;
	left: 0;
	top: 0;
	z-index: 1000;
	padding-top: 50px;
	overflow: hidden;
}
#main header .header_inner{
	position: relative;
	height: 100px;
}
.skils{
	position: absolute;
	left: 50%;
	top: 50px;
	width: 900px;
	color: #333;
	overflow: hidden;
	transform: translateX(-50%);
	text-align: left;
}
.skils .skils_inner{
	display: flex;
    justify-content: center;
	align-items: center;
	gap: 8vw;
}
.skils .skils_inner span{
	display: block;
}

.skils .skils_inner ul{
	position: relative;
}
.skils .skils_inner ul::after{
	content: "";
    position: absolute;
    right: -40px;
    top: 40px;
    width: 0.5px;
    height: 25px;
    background-color: #ccc;
}
.skils .skils_inner ul:last-child:after{
	display: none;
}


.skils .skils_inner ul li:first-child{
	margin-bottom: 10px;
	font-size: 0.875em;
	/* color: #fff; */
	color: #333;
	font-weight: 400;
}
.skils .skils_inner ul li{
	margin-bottom: 5px;
}
.skils .skils_inner ul li:nth-child(2){
	font-size: 1em;
	font-family: "Oswald", sans-serif;
}
.skils .skils_inner ul li:last-child{
	font-size: 0.875em;
	/* color: #ededed; */
	color: #333;
	font-weight: 300;
}

@media only screen and (max-width: 1000px){
	.skils{
		display: none;
	}
	
}

#main header .header_inner .namelogo{
	position: absolute;
	left: 0;
	width: 100px;
	margin-left: 2em;
	text-align: left;
}
#main header .header_inner .namelogo.none{
